      <div data-ui-id="IdeHome-Header" class=" ide--home-header bx--row">
        <div
          data-ui-id="IdeHome-Header-Text"
          class="ide--home-heading-container bx--col-sm-1 bx--col-md-2 bx--col-lg-6 bx--col-xlg-6 bx--col-max-6"
        >
          <div>
            <p class="ide--home-product-heading">{{ appData.main.title.intro }}</p>
            <p class="ide--home-call-to-action">{{ appData.main.title.text.join(' ') }}</p>
          </div>
        </div>
        <div
          data-ui-id="IdeHome-Header-Image"
          class="ide--home-image-container bx--col-sm-3 bx--col-md-6 bx--col-lg-10 bx--col-xlg-10 bx--col-max-10"
        >
          <img src="../../assets/message-flow-v2.svg" alt="Image representing a message flow" class="ide--home-image" />
        </div>
      </div>
      <div data-ui-id="IdeHome-Sets" class="bx--row bx--grid bx--grid--condensed ide--home-table">
        <section data-ui-id="IdeHome-Task-Set" class="bx--row ide--home-full-width">
          <div
            class="
        ide--card-container ide--card-container__standard ide--card-text bx--col-sm-1 bx--col-md-2 bx--col-lg-4 bx--col-xlg-4 bx--col-max-4"
            data-ui-id="ide--card-tutorials"
            v-for="link in cardLinks"
            :key="link.id"
          >
            <IdeCard :href="link.href" :label="link.label" :description="link.description" :icon="link.icon" />
          </div>
        </section>
      </div>
    </div>
  </section>
</template>

<script>
import IdeCard from '../IdeCard';
import appData from '../../app-data';

export default {
  name: 'IdeHome',
  components: { IdeCard },
  props: {
    links: Array,
  },
  created() {
    this.appData = appData;
  },
  computed: {
    cardLinks() {
      return this.links.filter(item => item.showOnCard);
    },
  },
};
</script>

