@import '../../styles/settings';

@import './IdeHome';
@import '../IdeNavigation/IdePageContent';

.Ide--content-alignment {
  margin-left: 0;
}
// use all width
.IdePageContent-use-width {
  width: 100%;
}

.toolkit-home {
  &.ide--home {
    padding-top: $layout-05;
  }

  .ide--home-header {
    flex-wrap: nowrap;
  }

  .ide--home-image-container {
    height: 100%;
    max-width: 100%;
    text-align: right;
  }

  .ide--home-image {
    height: 100%;
    padding-bottom: $spacing-08;
